
.aboutIcons, .aboutTitle{
    text-align: center;
    color:#000;
  }
  
  .aboutIcon{
    text-align: center;
    font-size:110px;
    color:#234261;
  }
  .cardBody{
    padding-top: 0px !important;
    color:#000;
  }
  
  .cardabout{
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
    padding:30px;
    transition-duration: 1s;
  }
  
  .cardabout:hover{
    margin-top:10px;
    transform:scale(1.15);
  }