.brandText {
    margin-top: 100px;
    margin-bottom: 60px;
    color: #cf404d;
    font-size: 40px;
    line-height: 25px;
    font-weight: 600;
    letter-spacing: 2px;
    cursor:pointer;
    font-family:'Courier New', Courier, monospace;
}

.header1{
  visibility: visible;
}

@media only screen and (max-width: 767px) {
  .header1 {
    visibility: hidden;
  }
}

.navlink{
    display: "block";
    margin: 25px 0px;
    color:#fff;
    font-size :18px;
    vertical-align: middle;
    text-align:left;
    letter-spacing:1px;
    cursor:pointer;
    text-transform: uppercase;
    text-decoration:"none" ;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    max-width: "100%";

}
.navlink:hover{
  color: #cf404d;
  text-decoration:"none";
  
}

.navIcon{
  font-size:30px !important;
  margin-top:-5px;
}