
.cardteam{
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
    padding:15px;
    border-radius: 50px;
    background-color:rgba(240, 241, 232, 0.705);
    text-align: center;
    cursor: pointer;
    transition-duration: 0.5s;
  
  }
  
  .cardteam:hover{
    margin-top:0px;
    transform:scale(1.05);
  }
  
  .teamimg{
    border:0px;
    border-radius:50%;
  }
  
.back2{
    position:absolute;
    padding:0px 0px;
    right: 0;
    
  }
  .aboutText{
    text-align: center;
    padding: 50px 40px;
    background-color:rgba(238, 240, 219, 0.705);
  }

  .abouth6{
    color: #234261;
    font-size:20px;
    font-weight: 400;
    margin-top: -15px;
    letter-spacing: 3px;
    padding:0px;
  }

  .aboutIcons, .aboutTitle{
    text-align: center;
    color:#000;
  }
  
  .aboutIcon{
    text-align: center;
    font-size:110px;
    color:#234261;
  }
  .cardBody{
    padding-top: 0px !important;
    color:#000;
  }
  
  .cardabout{
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.2), 0 1px 5px 0 rgba(0, 0, 0, 0.19);
    padding:30px;
  }
  
  .cardabout:hover{
    margin-top:10px;
    transform:scale(1.15);
  }